import React from 'react';
import auth from './auth';
import {withRouter} from 'react-router-dom';

export const AppLayout = (props) =>{
    return (
        <div>
            <h1>App Layout</h1>
            <button 
                onClick={() => {
                    auth.logout(() => {
                        props.history.push("/");
                    })
                }}
            >
                Logout
            </button>
        </div>
    );
};